<template>
  <div class="main-content"></div>
</template>
<script>
  export default {
    mounted () {
      if (localStorage.getItem('firstLogin') === '1') {
        this.$root.Bus.$emit('dialogOpenListener', {currentView: 'resetPassword', dialogFormVisible: true, title: '', isClose: false});
      }
    }
  };
</script>



