// 
function getLength (val) {
  let len = 0;
  for (var i = 0; i < val.length; i++) {
    if (val.charCodeAt(i) > 127 || val.charCodeAt(i) === 94) {
      len += 2;
    } else {
      len++;
    }
  }
  return len;
}
export function valideConvergeName (rule, value, callback) {
  if (!value) {
    callback(new Error(''));
  } else {
    if (getLength(value) > 40) {
      callback(new Error('40'));
    } else {
      callback();
    }
  }
}

// 
export function valideEnConvergeName (rule, value, callback) {
  let re = /^[a-zA-Z]+$/;
  if (!re.test(value)) {
    callback(new Error(''));
  } else if (value.length > 40) {
    callback(new Error('40'));
  } else {
    callback();
  }
}

// 
export function valideConverAddress (rule, value, callback) {
  let re = /^(http|https):\/\//;
  if (!re.test(value)) {
    callback(new Error('http/https'));
  } else {
    callback();
  }
}

// addEditUser
export function checkPhone (rule, value, callback) {
  if (!value) {
    return callback(new Error(''));
  } else {
    const reg = /^1[3|4|5|7|8][0-9]\d{8}$/;
    if (reg.test(value)) {
      callback();
    } else {
      return callback(new Error(''));
    }
  }
}

// addEditUser
export function lengthVal (rule, value, callback) {
  if (!value || value.length > 40) {
    return callback(new Error('40'));
  } else {
    callback();
  }
}

// addEditUserworkLengthVal
export function workLengthVal (rule, value, callback) {
  if (!value || value.length > 20) {
    return callback(new Error('20'));
  } else {
    callback();
  }
}



