<script>
  import TopBanner from '@/components/TopBanner.vue';
  import pcLogo from '@/assets/h5/pc_logo.png';
  import { companyGetAgreeTime, companyGetAgreeList } from '@/services/H5ServiceRequest';
  import Utils from '@/utils/utils.js';
  import moment from 'moment';
  export default {
    name: 'Agreement',
    data: function () {
      return {
        isPc: true,
        platformList: [],
        pcLogo,
        time: ''
      };
    },
    components: {
      TopBanner
    },
    created: function () {
      this.companyGetAgreeTime();
      this.companyGetAgreeList();
      this.isPc = Utils.isPc();
    },
    methods: {
      companyGetAgreeList () {
        companyGetAgreeList().then(res => {
          this.platformList = res.data;
        });
      },
      companyGetAgreeTime () {
        companyGetAgreeTime().then(res => {
          this.time = moment(res.data).format('YYYYMMDD');
        });
      }
    }
  };
</script>

